.App {
  text-align: center;
}
body{
  text-align: center;
}

.info{
  display: inline-block;
  font-family: sans-serif;
  border-radius: 8px;
  text-align: left;
  width: 340px;
  margin: 10px;
  padding: 10px;
  background-color: rgb(66, 66, 66);
  color: white;
}
button{
  padding: 8px 12px;
  border: 0px;
  margin: 10px;
  font-weight: 550;
  font-size: 1.7rem;
  background-color: rgb(52, 52, 58);
  color: white;
  border-radius: 5px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
